//cover bootstrap settings getting API methods
$utilities: map-merge(
  $utilities,
  (
    "overflow-x":(
      responsive: true,
      property: overflow-x,
      values: auto hidden visible scroll,
    ),
    
    "overflow-y":(
      responsive: true,
      property: overflow-y,
      values: auto hidden visible scroll,
    ),

    "border":
      map-merge(
        map-get($utilities, "border"),
        (
          responsive: true,
        )
      ),
    "border-top":
      map-merge(
        map-get($utilities, "border-top"),
        (
          responsive: true
        )
      ),
    "border-bottom":
      map-merge(
        map-get($utilities, "border-bottom"),
        (
          responsive: true
        )
      ),
    "border-start":
      map-merge(
        map-get($utilities, "border-start"),
        (
          responsive: true
        )
      ),
    "border-end":
      map-merge(
        map-get($utilities, "border-end"),
        (
          responsive: true
      
        )
      ),
    "width":
      map-merge(
        map-get($utilities, "width"),
        (
          responsive: true,
          //  map-merge map-get 
          values:
            map-merge(
              map-get(map-get($utilities, "width"), "values"),
              (
                10: 10%,
                20: 20%,
                30: 30%,
                40: 40%,
                60: 60%,
                70: 70%,
                80: 80%,
                90: 90%,
              )
            ),
        )
      ),
    "height":
      map-merge(
        map-get($utilities, "height"),
        (
          responsive: true,
          //  map-merge map-get 
          values:
            map-merge(
              map-get(map-get($utilities, "height"), "values"),
              (
                10: 10%,
                20: 20%,
                30: 30%,
                40: 40%,
                60: 60%,
                70: 70%,
                80: 80%,
                90: 90%,
              )
            ),
        )
      ),
    "font-weight":
      map-merge(
        map-get($utilities, "font-weight"),
        (
          values:
            map-merge(
              map-get(map-get($utilities, "font-weight"), "values"),
              (
                medium: $font-weight-medium,
              )
            ),
        )
      ),
  )
);


/* utilities special spacer */
.px-18{
  padding-left: 18px;
  padding-right: 18px;
}

.py-18{
  padding-top: 18px;
  padding-bottom: 18px;
}

.py-44{
  padding-top: 44px;
  padding-bottom: 44px;
}

/* width */
.w-40px{
  width: 40px;
}