/* swiper */

.swiper{
    &-container {
    width: 100%;
    height: 100%;
  }
    &-slide {
        img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        }
    }

}


/* components bg-img */

.bg-img{
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
/* components card-img */
.card-img{
  object-fit: cover;
  object-position: center;
}
